import {LEFT_MENU_EXPANDED} from "../../actions/app/app-settings";

const initState = {
    isNavMenuExpanded: true,
};

export default (state, action) => {
    if (state === undefined) {
        return initState;
    }

    switch (action.type) {
        case LEFT_MENU_EXPANDED:
            return {
                ...state,
                isNavMenuExpanded: !state.isNavMenuExpanded
            }
        default:
            return state;
    }
}